:root {
  --background-color: hsl(0, 0%, 5%);
  --text-color: hsl(45, 27%, 68%);
  --white-color: hsl(0, 0%, 100%);
}

body {
  background-color: var(--background-color);
}

:root {
  --main-font: "bebas neue", serif;
}

body {
  font-family: var(--main-font);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body,
ul {
  margin: 0;
  padding: 0;
  color: var(--whit-color);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.home {
  overflow-x: hidden;
}
.home__container img {
  height: 100vh;
  width: auto;
}

.nav {
  position: fixed;
  width: 100%;
  height: 80px;
  display: flex;
  background-color: var(--background-color);
}
.nav__logo img {
  margin-top: 10px;
  width: 150px;
  height: auto;
  margin-right: 400px;
}
.nav__links {
  margin-right: 45px;
  margin-top: 36px;
}
.nav__text {
  color: var(--text-color);
}
.nav__search {
  margin-top: 20px;
  margin-left: 150px;
  margin-right: 70px;
}
.nav__searchbox {
  color: var(--text-color);
  padding: 10px;
  background-color: var(--background-color);
  border: none;
  border-bottom: 3px solid var(--text-color);
}
@media only screen and (max-width: 1023px) {
  .nav {
    width: 0%;
    height: 0px;
  }
  .nav__logo {
    display: none;
  }
  .nav__links {
    display: none;
  }
  .nav__search {
    display: none;
  }
  .nav__menu {
    position: fixed;
    top: 32px;
    left: 90%;
  }
  .nav__menu.open span:first-child {
    transform: rotate(45deg);
  }
  .nav__menu.open span:nth-child(2) {
    opacity: 0;
  }
  .nav__menu.open span:last-child {
    transform: rotate(-45deg);
  }
  .nav__menu span {
    display: block;
    width: 26px;
    height: 2px;
    background-color: var(--text-color);
    transform-origin: 4px 1px;
    transition: all 300ms ease-in-out;
  }
  .nav__menu span:not(:last-child) {
    margin-bottom: 4px;
  }
}